import React from 'react';
// Change BrowserRouter to HashRouter here
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import WeightTracker from './components/WeightTracker/WeightTracker';
import './App.css';

const App = () => {
  return (
    // Router component remains the same (no basename needed)
    <Router>
      <div className="App dark-mode">
        <Routes>
          {/* Your route path remains "/" because HashRouter handles the base path */}
          <Route path="/" element={<WeightTracker />} />
          {/* Add other routes here if needed */}
        </Routes>
      </div>
    </Router>
  );
};

export default App;